.nav__wrapper {
  justify-content: space-between;
  align-items: baseline;
  margin: 0;
  padding-top: 45px;
  padding-bottom: 45px;
  display: flex;
}

.nav__wrapper a {
  color: #fff;
  align-items: baseline;
  text-decoration: none;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  display: flex;
}

.nav__item {
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.167;
}

.nav__item:not(:first-child) {
  margin-left: 30px;
}

.nav__link {
  color: #fff;
  position: relative;
}

.nav__link:after {
  content: "";
  background: #ff6b08;
  border-radius: 2px;
  width: 0;
  height: 3px;
  transition: width .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: -6px;
  right: 0;
}

.nav__link:hover:after, .nav__link:focus:after {
  width: 100%;
  left: 0;
}

.nav__link.nav__link--active {
  position: relative;
}

.nav__link.nav__link--active:after {
  content: "";
  background: #ff6b08;
  border-radius: 2px;
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -6px;
}

.nav__account {
  position: relative;
}

.account__list {
  z-index: 1;
  text-align: center;
  border-bottom: 2px solid #ff6b08;
  border-left: 2px solid #ff6b08;
